.major {
  margin-bottom: 80px;

  &__list {
    grid-template-rows: repeat(2, 1fr);
    row-gap: 29px;

    @include small-tablet {
      grid-template-rows: initial;
    }
  }

  &__item {
    border-radius: 5px;
    background: #000;
    background-size: cover;
    padding: 30px 20px 23px 20px;
    grid-column: 4 span;
    grid-row: 1 span;
    min-height: 265px;
    flex-direction: column;
    align-items: start;
    justify-content: space-between;

    @include tablet {
      grid-column: 4 span;
    }

    @include small-tablet {
      grid-column: 6 span;
      width: 100%;
    }

    &--big {
      grid-row: 2 span;
      min-height: 560px;

      @include small-tablet {
        grid-row: initial;
        min-height: 265px;
      }
    }

    &--background-01 {
      background: url(../img/major/cover-01.jpg) right no-repeat;
      background-size: cover;
    }

    &--background-02 {
      background: url(../img/major/cover-02.jpg) no-repeat;
      background-size: cover;
    }

    &--background-03 {
      background: url(../img/major/cover-03.jpg) no-repeat;
      background-size: cover;
    }

    &--background-04 {
      background: url(../img/major/cover-04.jpg) no-repeat;
      background-size: cover;
    }

    &--background-05 {
      background: url(../img/major/cover-05.jpg) no-repeat;
      background-size: cover;
    }
  }


  &__text {

  }

  &__subtitle {
    margin: 0;
    margin-bottom: 6px;
    color: var(--white-color);
    font-size: 14px;
  }

  &__profile {
    color: var(--white-color);
    font-size: 14px;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--accent-color);
    }

    &:active {
      color: var(--accent-color-active);
    }
  }

  &__title {
    margin: 0;
  }

  &__link {
    color: var(--white-color);
    font-size: 26px;
    font-weight: 700;
    line-height: 138.462%;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--white-color-hover);
    }

    &:active {
      color: var(--white-color-acitve);
    }
  }
}

.major__profile.focus-visible {
  outline: 1.5px solid var(--white-color);
}

.major__link.focus-visible {
  outline: 1.5px solid var(--white-color);
}
