.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  border-radius: 5px;
  background-color: #232323;
  color: var(--white-color);
  font-size: 14px;
  font-weight: 700;
  transition: background-color 0.3s ease-in-out;

  &:focus {
    outline: none;
  }

  @include hover {
    background-color: #232323b9;
  }

  &:active {
    background-color: #23232386;
  }
}

.btn.focus-visible {
  background-color: #23232386;
}


