.popular {
    padding-bottom: 80px;

    &__label {
      margin: 0;
      margin-bottom: 27px;
      color: var(--text-color-dark);
      font-size: 30px;
      font-weight: 700;
    }

    &__list {
      row-gap: 15px;
    }

    &__item {
      grid-column: 3 span;

      @include tablet {
        grid-column: 2 span;
      }

      @include small-tablet {
        grid-column: 3 span;
      }
    }

    &__top {
      position: relative;
      margin-bottom: 20px;

      img {
        width: 100%;
        height: 100%;
      }
    }

    &__category {
      position: absolute;
      top: 20px;
      left: 20px;
    }

    &__title {
      margin: 0;
      margin-bottom: 10px;
    }

    &__link {
      color: var(--text-color-dark);
      font-size: 20px;
      font-weight: 700;
      line-height: 140%;

      &:focus {
        outline: none;
      }

      @include hover {
        color: var(--text-color-dark-hover);
      }

      &:active {
        color: var(--text-color-dark-active);
      }
    }

    &__subtitle {
      margin: 0;
      color: var(--text-color-grey);
      font-size: 14px;
      font-style: italic;
    }

    &__profile {
      color: var(--text-color-grey);
      font-size: 14px;
      font-style: italic;

      &:focus {
        outline: none;
      }

      @include hover {
        color: var(--accent-color);
      }

      &:active {
        color: var(--accent-color-active);
      }
    }
}

.popular__category.focus-visible {
  outline: 1.5px solid var(--text-color-dark);
}

.popular__link.focus-visible {
  outline: 1.5px solid var(--text-color-dark);
}

.popular__profile.focus-visible {
  outline: 1.5px solid var(--text-color-dark);
}
