.header {
  background-color: var(--black-color);
  padding: 34px 0;
  margin-bottom: 30px;

  &__container {
    align-items: center;
    justify-content: space-between;
  }

  &__wrapper {
    align-items: center;
    justify-content: space-between;
    width: 65%;
    @include small-tablet {
      position: fixed;
      left: 0;
      top: 0;
      width: 80%;
      height: 100vh;
      padding: 80px 16px;
      background-color: #0e0e0e;
      z-index: 100;
      transform: translateX(-100%);
      visibility: hidden;
      transition: transform 0.3s ease-in-out, visibility 0.3s ease-in-out;
      overflow-y: auto;
      align-items: start;
    }
  }

  &__seacrh {
    padding: 1.5px;

    path {
      fill: #929eaf;
      transition: fill 0.3s ease-in-out;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      path {
        fill: #929eafab;
      }
    }

    &:active {
      path {
        fill: #929eaf7a;
      }
    }
  }
}

.nav {

  &__list {
    align-items: center;

    @include small-tablet {
      align-items: start;
      flex-direction: column;
    }
  }

  &__item {
    margin-right: 38px;

    &:last-child {
      margin-right: 0;
    }

    @include small-tablet {
      margin-right: 0;
      margin-bottom: 25px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__link {
    color: var(--white-color);
    font-size: 14px;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--white-color-hover);
    }

    &:active {
      color: var(--white-color-active);
    }

    @include small-tablet {
      font-size: 21px;
    }
  }
}

.nav__link.focus-visible {
  outline: 1.5px solid var(--white-color);
}

.header__seacrh.focus-visible {
  outline: 1.5px solid var(--white-color);
}

.header__wrapper.menu--active {
  visibility: visible;
  transform: none;
}
