.main-block {
  padding-bottom: 80px;
}

.main-article {
  grid-column: 8 span;

  @include tablet {
    grid-column: 6 span;
  }

  @include small-tablet {
    grid-column: 4 span;
  }

  @include mobile {
    grid-column: 6 span;
  }

  &__list {
    grid-template-columns: repeat(8, 1fr);
    margin-bottom: 200px;

    @include tablet {
      grid-template-columns: repeat(6, 1fr);
    }

    @include small-tablet {
      grid-template-columns: repeat(4, 1fr);
    }

    @include mobile {
      grid-template-columns: repeat(6, 1fr);
      margin-bottom: 300px;
    }
  }

  &__item {
    grid-column: 8 span;
    padding: 0 30px;
    min-height: 445px;
    margin-bottom: 180px;

    @include tablet {
      grid-column: 6 span;
    }

    @include small-tablet {
      grid-column: 4 span;
      margin-bottom: 230px;
    }

    @include mobile {
      grid-column: 6 span;
    }

    &--background-01 {
      background: url(../img/main-block/article/cover-01.png) center no-repeat;
      background-size: cover;
    }

    &--background-02 {
      background: url(../img/main-block/article/cover-02.png) center no-repeat;
      background-size: cover;
    }

    &--background-03 {
      background: url(../img/main-block/article/cover-03.png) center no-repeat;
      background-size: cover;
    }

    &--background-04 {
      background: url(../img/main-block/article/cover-04.png) center no-repeat;
      background-size: cover;
    }

    &--background-05 {
      background: url(../img/main-block/article/cover-05.png) center no-repeat;
      background-size: cover;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__text {
    position: relative;
    top: 315px;
    left: 0;
    padding: 30px;
    border-radius: 5px;
    background: var(--white-color);
    box-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.10);

    @include mobile {
      top: 220px;
    }
  }

  &__category {
    margin-bottom: 20px;
  }

  &__title {
    margin: 0;
    margin-bottom: 15px;
  }

  &__link {
    color: var(--text-color-dark);
    font-size: 25px;
    font-weight: 700;
    line-height: 112%;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--text-color-dark-hover);
    }

    &:active {
      color: var(--text-color-dark-active);
    }
  }


  &__subtitle {
    margin-bottom: 14px;
    color: var(--text-color-grey);
    font-size: 14px;
    font-style: italic;
  }

  &__profile {
    color: var(--text-color-grey);
    font-size: 14px;
    font-style: italic;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--accent-color);
    }

    &:active {
      color: var(--accent-color-active);
    }
  }

  &__descr {
    margin: 0;
    color: var(--text-color-grey);
    font-family: PT Sans;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px; /* 171.429% */
  }
}

.main-article__link.focus-visible {
  outline: 1.5px solid var(--text-color-dark);
}

.main-article__profile.focus-visible {
  outline: 1.5px solid var(--text-color-dark);
}

.main-article__category.focus-visible {
  outline: 1.5px solid var(--text-color-dark);
}

.pagination {

  @include mobile {
    margin-bottom: 50px;
  }

  &__left {
    border-radius: 3px;
    border: 1px solid #E5E5E5;
    width: 40px;
    height: 40px;
    margin-right: 10px;
  }

  &__right {
    border-radius: 3px;
    border: 1px solid #E5E5E5;
    width: 40px;
    height: 40px;
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 3px;
    border: 1px solid #E5E5E5;
    width: 40px;
    height: 40px;
    margin-right: 10px;

    &--active {
      background-color: var(--black-color);
    }
  }

  &__btn {
    color: var(--text-color-dark);
    font-size: 16px;
    line-height: 100%;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--text-color-dark-hover);
    }

    &:active {
      color: var(--text-color-dark-active);
    }

    &--active {
      color: var(--white-color);

      &:focus {
        outline: none;
      }

      @include hover {
        color: var(--white-color-hover);
      }

      &:active {
        color: var(--white-color-acitve);
      }
    }
  }
}

.pagination__btn.focus-visible {
  outline: 1.5px solid var(--text-color-dark);
}

.pagination__btn--active.focus-visible {
  outline: 1.5px solid var(--white-color);
}

.sidebar {
  grid-column: 10 / 13;

  @include tablet {
    grid-column: 7 / 9;
  }

  @include small-tablet {
    grid-column: 5 / 7;
  }

  @include mobile {
    grid-column: 6 span;
  }

  .post-sidebar {
    margin-bottom: 75px;

    @include mobile {
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    &__top {
      margin-bottom: 20px;
    }

    &__name {
      color: var(--text-color-dark);
      font-size: 18px;
      font-weight: 700;
      margin-bottom: 8px;
    }

    &__subname {
      margin-bottom: 14px;
      color: #999;
      font-size: 14px;
      font-style: italic;
    }

    &__descr {
      margin: 0;
      margin-bottom: 21px;
      color: #666;
      font-size: 15px;
      line-height: 166.667%;
    }

    &__button {
      padding: 16px 0;
      width: 100%;
    }
  }

  .popular-side {

    .popular-side__category.focus-visible {
      outline: 1.5px solid var(--text-color-dark);
    }

    .popular-side__link.focus-visible {
      outline: 1.5px solid var(--text-color-dark);
    }

    .popular-side__profile.focus-visible {
      outline: 1.5px solid var(--text-color-dark);
    }


    &__label {
      margin: 0;
      margin-bottom: 23px;
      color: var(--text-color-dark);
      font-size: 18px;
      font-weight: 700;
    }

    &__list {
    }

    &__item {
      margin-bottom: 39px;

      @include mobile {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
      }
    }

    &__top {
      position: relative;
      margin-bottom: 20px;
      width: 100%;
      height: 100%;

      img {
        width: 100%;
        height: 100%;
      }
    }

    &__category {
      position: absolute;
      top: 20px;
      left: 20px;
    }

    &__title {
      margin: 0;
      margin-bottom: 10px;
    }

    &__link {
      color: var(--text-color-dark);
      font-size: 20px;
      font-weight: 700;
      line-height: 140%;

      &:focus {
        outline: none;
      }

      @include hover {
        color: var(--text-color-dark-hover);
      }

      &:active {
        color: var(--text-color-dark-active);
      }
    }

    &__subtitle {
      margin: 0;
      color: var(--text-color-grey);
      font-size: 14px;
      font-style: italic;
    }

    &__profile {
      color: var(--text-color-grey);
      font-size: 14px;
      font-style: italic;

      &:focus {
        outline: none;
      }

      @include hover {
        color: var(--accent-color);
      }

      &:active {
        color: var(--accent-color-active);
      }
    }
  }

  .instagram-side {
    margin-bottom: 60px;

    &__title {
      margin: 0;
      margin-bottom: 25px;
      color: var(--text-color-dark);
      font-size: 18px;
      font-weight: 700;
      line-height: 116.667%;
    }

    &__images {
      margin-bottom: 10px;
    }

    &__button {
      width: 100%;
      padding: 17px 0;

      svg {
        margin-right: 10px;
      }
    }
  }

  .tags-sidebar {
    margin-bottom: 60px;

    .tags-sidebar__link.focus-visible {
      outline: 1.5px solid var(--text-color-dark);
    }

    &__title {
      margin: 0;
      margin-bottom: 26px;
      color: var(--text-color-dark);
      font-size: 18px;
      font-weight: 700;
      line-height: 116.667%;
    }

    &__list {
      flex-wrap: wrap;
      gap: 10px;
    }

    &__item {

      &:last-child {
        margin: 0;
      }
    }

    &__link {
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      color: var(--text-color-dark);
      font-size: 14px;
      border-radius: 3px;
      background-color: #ebebeb;
      padding: 7px 16px 5px 16px;
      transition: background-color 0.3s ease-in-out;

      &:focus {
        outline: none;
      }

      @include hover {
        background-color: #c9c9c9;
      }

      &:active {
        background-color: #888888;
      }
    }
  }

  .form-sidebar {

    &__title {
      margin: 0;
      margin-bottom: 11px;
      color: var(--text-color-dark);
      font-size: 18px;
      font-weight: 700;
    }

    &__descr {
      margin: 0;
      margin-bottom: 27px;
      color: var(--text-color-grey);
      font-size: 14px;
      line-height: 171.429%;
    }

    &__input {
      margin-bottom: 10px;
      width: 100%;
      padding: 17px 15px;
      border-radius: 3px;
      border: 1px solid #E5E5E5;
      background: var(--white-color);
      color: #AEB3BB;

      &::placeholder {
        color: #AEB3BB;
        font-size: 14px;
        font-family: var(--font-family);
      }
    }

    &__btn {
      width: 100%;
      padding: 17px 15px 15px 15px;
    }
  }
}

