.footer {
  background-color: #fff;

  &__top {
    background-color: var(--text-color-dark);
    padding: 0 10px;
  }

  &__bottom {
    padding: 0 10px;
    background-color: var(--black-color);
  }

  &__left {
    grid-column: 3 span;

    @include tablet {
      grid-column: 4 span;
      order: 1;
      margin-bottom: 50px;
    }

    @include small-tablet {
      grid-column: 3 span;
    }

    @include mobile {
      grid-column: 6 span;
    }
  }

  &__center {
    grid-column: 5 / 9;

    @include tablet {
      grid-column: 3 / 7;
      order: 3;
    }

    @include tablet {
      grid-column: 2 / 6;
    }

    @include mobile {
      grid-column: 6 span;
      order: 2;
      margin-bottom: 50px;
    }
  }

  &__right {
    grid-column: 9 / 12;

    @include tablet {
      grid-column: 4 span;
      order: 2;
    }

    @include small-tablet {
      grid-column: 3 span;
    }

    @include mobile {
      grid-column: 6 span;
      order: 3;
    }
  }

  &__copy {
    color: var(--white-color);
    font-size: 14px;
    transition: color 0.2s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      text-decoration: underline;
    }

    &:active {
      color: var(--white-color-active);
    }
  }

  &__container {

    &--top {
      padding: 62px 0 49px 0;
    }

    &--bottom {
      justify-content: space-between;
      align-items: center;
      padding: 39px 0 37px 0;

      @include small-tablet {
        justify-content: center;
      }
    }
  }

  &__logo {
    display: block;
    margin-bottom: 28px;
  }

  &__descr {
    margin: 0;
    margin-bottom: 64px;
    color: #cecece;
    font-size: 14px;
    line-height: 157.143%;
  }

  &__title {
    margin: 0;
    color: var(--white-color);
    font-size: 20px;
    font-weight: 700;

    &--margin-01 {
      margin-bottom: 23px;
    }

    &--margin-02 {
      margin-bottom: 28px;
    }

    &--margin-03 {
      margin-bottom: 38px;
    }
  }
}

.social {

  &__item {
    margin-right: 15px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    rect {
      fill: #373737;
      transition: fill 0.3s ease-in-out;
    }
    path {
      fill: var(--white-color);
      transition: fill 0.3s ease-in-out;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      rect {
        fill: #636363;
      }
      path {
        fill: var(--white-color-hover);
      }
    }

    &:active {
      rect {
        fill: #919191;
      }
      path {
        fill: var(--white-color-active);
      }
    }
  }
}

.news-footer {
  flex-direction: column;
  row-gap: 25px;

  &__item {
    align-items: center;
  }

  &__label {
    display: block;
    color: var(--white-color);
    font-size: 15px;
    font-weight: 700;
    line-height: 173.333%;
    margin-bottom: 13px;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--white-color-hover);
    }

    &:active {
      color: var(--white-color-active);
    }
  }

  &__sublabel {
    color: #CECECE;
    font-size: 14px;
    font-style: italic;
    line-height: 100%;
  }

  &__profile {
    color: #CECECE;
    font-size: 14px;
    font-style: italic;
    line-height: 100%;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--accent-color);
    }

    &:active {
      color: var(--accent-color-active);
    }
  }

  &__left {
    margin-right: 20px;


    &:last-child {
      margin-right: 0;
    }

    img {
      border-radius: 3px;
      min-width: 95px;
      min-height: 95px;
    }
  }
}

.form-footer {

  &__title {
    margin: 0;
    margin-bottom: 11px;
    color: var(--text-color-dark);
    font-size: 18px;
    font-weight: 700;
  }

  &__descr {
    margin: 0;
    margin-bottom: 27px;
    color: var(--text-color-grey);
    font-size: 14px;
    line-height: 171.429%;
  }

  &__input {
    margin-bottom: 10px;
    width: 100%;
    padding: 17px 15px;
    border-radius: 3px;
    background: #373737;
    color: #AEB3BB;

    &::placeholder {
      color: #AEB3BB;
      font-size: 14px;
      font-family: var(--font-family);
    }
  }

  &__btn {
    width: 100%;
    padding: 17px 15px 15px 15px;

    @include hover {
      background-color: #383838;
    }

    &:active {
      background-color: #4d4d4d;
    }
  }
}

.nav--footer {
  @include small-tablet {
    display: none;
  }
}

.social__link.focus-visible {
  rect {
    fill: #919191;
  }
  path {
    fill: var(--white-color-active);
  }
}

.news-footer__label.focus-visible {
  outline: 1.5px solid var(--white-color);
}

.news-footer__profile.focus-visible {
  outline: 1.5px solid var(--accent-color);
}

.form-footer__btn.focus-visible {
  background-color: #4d4d4d;
}

.footer__copy.focus-visible {
  outline: 1.5px solid var(--white-color);
}
